<template>
    <div class="content">
        <div class="prompt"><i class="el-icon-info"></i>实时更新中</div>
        <div class="card">
            <div class="item">
                <span>总抖音数</span>
                <div>{{ data.system.total }}</div>
            </div>
            <div class="divider"></div>
            <div class="item">
                <span>总辅助认证数</span>
                <div>{{ data.system.verify }}</div>
            </div>
        </div>

        <div class="card">
            <div class="item">
                <span>今日收单</span>
                <div>{{ data.today.collect }}</div>
            </div>
            <div class="divider"></div>
            <div class="item">
                <span>今日做单</span>
                <div>{{ data.today.verify }}</div>
            </div>
            <div class="divider"></div>
            <div class="item">
                <span>等待接单</span>
                <div>{{ data.extra.waiting }}</div>
            </div>
        </div>

        <div class="card">
            <div class="item">
                <span>昨日收单</span>
                <div>{{ data.yesterday.collect }}</div>
            </div>
            <div class="divider"></div>
            <div class="item">
                <span>昨日做单</span>
                <div>{{ data.yesterday.verify }}</div>
            </div>
            <div class="divider"></div>
            <div class="item">
                <span>总异常单</span>
                <div>{{ data.extra.exception }}</div>
            </div>
        </div>

        <div class="card" v-if="data.list.yesterday_verify.length!=0||data.list.today_verify.length!=0">
            <div class="list" v-if="data.list.yesterday_verify.length!=0">
                <h6>昨日做单榜单</h6>
                <ol>
                    <li v-for="(d, i) in data.list.yesterday_verify" :key="'yesterday_verify_'+i">{{ d.account }} === {{ d.total }}</li>
                </ol>
            </div>
            <div class="divider" v-if="data.list.yesterday_verify.length!=0&&data.list.today_verify.length!=0"></div>
            <div class="list" v-if="data.list.today_verify.length!=0">
                <h6>今日做单榜单</h6>
                <ol>
                    <li v-for="(d, i) in data.list.today_verify" :key="'today_verify_'+i">{{ d.account }} === {{ d.total }}</li>
                </ol>
            </div>
        </div>
        <div class="card" v-if="data.list.yesterday_collect.length!=0||data.list.today_collect.length!=0">
            <div class="list" v-if="data.list.yesterday_collect.length!=0">
                <h6>昨日收单榜单</h6>
                <ol>
                    <li v-for="(d, i) in data.list.yesterday_collect" :key="'yesterday_collect_'+i">{{ d.account }} === {{ d.total }}</li>
                </ol>
            </div>
            <div class="divider" v-if="data.list.yesterday_collect.length!=0&&data.list.today_collect.length!=0"></div>
            <div class="list" v-if="data.list.today_collect.length!=0">
                <h6>今日收单榜单</h6>
                <ol>
                    <li v-for="(d, i) in data.list.today_collect" :key="'today_collect_'+i">{{ d.account }} === {{ d.total }}</li>
                </ol>
            </div>
        </div>

        <div class="addbalance">
            <h6>账户加量</h6>
            <el-row type="flex" align="middle" justify="space-around">
                <el-input v-model="form.account" style="max-width: 200px" placeholder="账号名" @blur="getbalance"></el-input>
                <el-input-number v-model="form.amount" :min="1" :max="1000" label="加量"></el-input-number>
                <el-button type="primary" plain @click="addbalance">确认加量</el-button>
            </el-row>
            <div style="font-size: 14px;color: #888888;margin-top: 10px;" v-if="loading==false && form.account!='' && balance >= 0">账号【{{role}}】：{{ form.account }} 的余量为 {{ balance }} 单</div>
            <div style="font-size: 14px;color: #888888;margin-top: 10px;" v-if="loading==false && form.account!='' && balance < 0">账号不存在</div>
        </div>
    </div>
</template>

<script>
import request from "@/request";

export default {
    name: "Statistics",
    metaInfo: {
        title: '监控',
    },
    timer: null,
    beforeMount() {
        this.getData();
        this.timer = setInterval(this.getData, 60*1000);
    },
    data: function (){
        return {
            data: {
                system: {
                    total: 0,
                    verify: 0
                },
                today: {
                    collect: 0,
                    verify: 0
                },
                yesterday: {
                    collect: 0,
                    verify: 0
                },
                extra: {
                    waiting: 0,
                    exception: 0
                },
                list: {
                    yesterday_collect: [],
                    today_collect: [],
                    yesterday_verify: [],
                    today_verify: []
                }
            },
            form: {
                account: '',
                amount: 1
            },
            role: '加载中...',
            balance: 0,
            loading: true
        };
    },
    methods: {
        getData: function () {
            request.User.statistics().then(res=>{
                this.data = res.data;
            });
        },
        getbalance: function () {
            this.loading = true;
            request.User.getbalance(this.form.account).then(res=>{
                this.loading = false;
                if (res.status > 0) {
                    this.balance = res.data.balance;
                    switch (res.data.role) {
                        case 0:
                            this.role = '超级管理员';
                            break;
                        case 1:
                            this.role = '收单员';
                            break;
                        case 2:
                            this.role = '做单员';
                            break;
                    }
                }else{
                    this.balance = -1;
                }
            });
        },
        addbalance: function () {
            request.User.addbalance(this.form.account, this.form.amount).then(res=>{
                this.loading = false;
                if (res.status > 0) {
                    this.$message({
                        message: '操作成功！',
                        type: 'success'
                    });
                    this.getbalance();
                    this.form.account = '';
                }else{
                    this.$message.error('操作失败，请重试！');
                }
            });
        }
    },
    beforeDestroy() {
        if (this.timer != null) {
            clearInterval(this.timer);
        }
    }
}
</script>

